.buttons {
    bottom: 0px;
    justify-content: flex-end !important;
    margin-bottom: 123px;
    position: absolute;
    width: 100%;
    z-index: 99;
}

.buttonIcon {
    height: 35px;
    margin-right: 7px;
    width: 35px;
}

.container {
    height: 470px;
    overflow: hidden;
    position: relative;
    width: 400px;
}

.image {
    border-radius: 10px;
    height: 470px;
    position: absolute;
    width: 200px;
}

.message {
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 10px;
    bottom: 0px;
    color: var(--white);
    font-size: large;
    height: 140px;
    margin-bottom: 0;
    padding: 30px 20px 20px 20px;
    position: absolute;
    width: 100%;
}

@media (max-width: 1123px) {
    .buttons {
        margin-bottom: 113px;
    }

    .container {
        height: 430px;
        width: 350px;
    }

    .image {
        height: 430px;
        width: 170px;
    }

    .message {
        font-size: medium;
        height: 130px;
    }
}